.map-marker {
  background-color: #ffffff;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  border: 2px solid #FF851B; }

.current-location-marker {
  background-color: #e74c3c;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  border: 3px solid #eaa29b;
  -webkit-animation: pulse 2s;
          animation: pulse 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(246, 126, 100, 0.4);
            box-shadow: 0 0 0 0 rgba(246, 126, 100, 0.4); }
  70% {
    -webkit-box-shadow: 0 0 0 30px rgba(246, 126, 100, 0);
            box-shadow: 0 0 0 30px rgba(246, 126, 100, 0); }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(246, 126, 100, 0);
            box-shadow: 0 0 0 0 rgba(246, 126, 100, 0); } }

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(246, 126, 100, 0.4);
            box-shadow: 0 0 0 0 rgba(246, 126, 100, 0.4); }
  70% {
    -webkit-box-shadow: 0 0 0 30px rgba(246, 126, 100, 0);
            box-shadow: 0 0 0 30px rgba(246, 126, 100, 0); }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(246, 126, 100, 0);
            box-shadow: 0 0 0 0 rgba(246, 126, 100, 0); } }



